$base-color: #00171F;
$base-color-1: #273469;
$base-color-2: #30343F;
$base-color-3: #00A8E8;
$base-color-4: #FAFAFF;
@mixin flex {
    display: flex;
    justify-content: center;
    align-items: end;
    min-height: 100vh;
    width: 100%;
}
body {
    @include flex;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background: rgb(238, 238, 238) ;
}
#root, .App {
    @include flex;
}
